"use server";

import { StudioProfile, saveStudioProfile, getStudioProfile } from "@/lib/db-studio";

export async function registerStudio(data: StudioProfile) {
    console.log("Registering studio:", data);
    try {
        saveStudioProfile(data);
        return { success: true, message: "Studio profile saved successfully." };
    } catch (e: any) {
        return { success: false, message: e.message };
    }
}

export async function fetchStudioProfile() {
    return getStudioProfile();
}
