import fs from 'fs';
import path from 'path';

const STUDIO_DB_PATH = path.join(process.cwd(), 'data', 'studio.json');

// --- Types ---

export interface TimeSlotPrice {
    start: string; // "10:00"
    end: string;   // "18:00"
    price: number;
}

export interface DaySchedule {
    slots: TimeSlotPrice[];
}

export interface RoomPricing {
    weekday: DaySchedule; // 平日
    saturday: DaySchedule; // 土曜
    sundayHoliday: DaySchedule; // 日祝
}

export interface Room {
    id: string;
    name: string; // "A Studio"
    pricing: RoomPricing;
}

export interface EquipmentOption {
    name: string;
    pricePerHour: number;
}

export interface BusinessHours {
    weekday: string;
    saturday: string;
    sundayHoliday: string;
}

export interface StudioProfile {
    // Basic Info
    storeName: string;
    companyName?: string;
    representative: string;
    manager: string;
    contactPerson: string;
    address: string;
    phone: string;
    email: string;
    businessHours: BusinessHours;
    url: string;
    studioCount: number;

    // Detailed Config
    rooms: Room[];
    equipmentOptions: EquipmentOption[];

    // Legacy/Simple text fallback
    options?: string;
}

// Initialize Studio DB
if (!fs.existsSync(STUDIO_DB_PATH)) {
    fs.writeFileSync(STUDIO_DB_PATH, JSON.stringify({}, null, 2));
}

export const getStudioProfile = (): StudioProfile | null => {
    try {
        const data = fs.readFileSync(STUDIO_DB_PATH, 'utf-8');
        const json = JSON.parse(data);
        return Object.keys(json).length > 0 ? json : null;
    } catch (error) {
        return null;
    }
};

export const saveStudioProfile = (profile: StudioProfile): void => {
    fs.writeFileSync(STUDIO_DB_PATH, JSON.stringify(profile, null, 2));
};
